import { useLoader } from "@Context/LoaderContext";
import React, { createContext, useContext, useEffect, useState } from "react";
import { io } from "socket.io-client";

// Cration d'un contexte pour la gestion de la connexion via Socket.io
const SocketContext = createContext();
// https://pitfalls.onrender.com
const socketIo = io("https://pitfalls.onrender.com", {
  transports: ["websocket"],
  reconnection: true,
});

export const SocketProvider = ({ children }) => {
  // Initialisation de la socket et des tats d'utilisateur et d'authentification
  const socket = socketIo;
  const [user, setUser] = useState({
    id: null,
    username: null,
    token: null,
    isGuest: false,
  });
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const { hideLoader, showLoader } = useLoader();

  // Vrifie la prsence d'un token pour rauthentifier l'utilisateur au chargement
  useEffect(() => {
    const token = localStorage.getItem("token");

    if (token) {
      showLoader();
      // met un vnement pour valider le jeton auprs du serveur
      socket.emit("user:validate-token", { token }, (response) => {
        hideLoader();
        if (response?.success) {
          let id = response.data.id;
          let username = response.data.username;
          // Met  jour les informations de l'utilisateur si le jeton est valide
          setUser({
            id,
            username,
            token,
          });
          setIsAuthenticated(true);
        } else {
          // Supprime le jeton en cas d'chec et affiche un message d'erreur
          localStorage.removeItem("token");
          hideLoader();
          console.info(response?.message || "Token manquant.");
        }
      });
    }
    // eslint-disable-next-line
  }, []);

  // Fonction pour connecter un utilisateur et stocker son jeton
  const login = (data) => {
    if (!data || !data.token || typeof data.token !== "string") {
      console.error("Donnes de connexion manquantes."); // Vrifie si les donnes sont prsentes
      return;
    }
    // Stocke le jeton et met  jour l'tat utilisateur
    localStorage.setItem("token", data.token);
    setUser(data);

    setIsAuthenticated(true);
  };

  // Fonction pour dconnecter un utilisateur et rinitialiser l'tat
  const logout = () => {
    const username = user.username; // Rcupre le nom d'utilisateur actuel
    if (!username) {
      console.error("Aucun utilisateur  dconnecter."); // Vrifie si un utilisateur est connect
      return;
    }

    // Supprime le jeton stock dans le navigateur
    localStorage.removeItem("token");

    // Informe le serveur de la dconnexion
    socket.emit("user:logout", { user }, (response) => {
      if (!response.success) {
        console.error(response.message || "Erreur lors de la dconnexion.");
      }
    });

    // Rinitialise l'tat utilisateur et l'authentification
    setUser({
      id: null,
      username: null,
      token: null,
      isGuest: false,
    });
    setIsAuthenticated(false);
  };

  // Fournit le contexte aux composants enfants
  return (
    <SocketContext.Provider
      value={{ socket, user, isAuthenticated, login, logout }}
    >
      {children}
    </SocketContext.Provider>
  );
};

// Hook personnalis pour accder facilement au contexte d'authentification
export const useAuth = () => useContext(SocketContext);
