import { useAuth } from "@Context/SocketContext";

function WelcomerMessages() {
  const { user } = useAuth();

  const heure = new Date().getHours();
  const phrases = {
    night: [
      ["Les vrais joueurs sont encore l, pas vrai", "?"],
      ["Prt  enchaner les kilomtres de nuit", "?"],
      ["Une session nocturne", "?"],
      ["C'est  cette heure que les lgendes se forment", "!"],
      ["Pas de repos pour les champions", "!"],
    ],
    day: [
      ["Bonne journe", "!"],
      ["Du soleil et des victoires", "!"],
      ["Encore une nouvelle victoire", "?"],
      ["Bonne chance", "!"],
      ["Une petite partie", "?"],
    ],
    evening: [
      ["Prt  faire des merveilles ce soir", "?"],
      ["Le soir, c'est l'heure des pros", "!"],
      ["Une petite partie", "?"],
      ["La soire ne fait que commencer", "!"],
      ["Finissez la journe en beaut", "!"],
    ],
  };

  const timeOfDay =
    heure >= 0 && heure < 5
      ? "night"
      : heure >= 5 && heure < 18
      ? "day"
      : "evening";

  const [phrase, symbol] =
    phrases[timeOfDay][Math.floor(Math.random() * phrases[timeOfDay].length)];

  return (
    <p className="welcomer">
      {phrase} <span>{user.username}</span> {symbol}
    </p>
  );
}

export default WelcomerMessages;
